


















































import Vue from "vue";
import AppHeader from "../components/AppHeader.vue";

let vm = Vue.component("register-view", {
  components: {
    child: { AppHeader },
  },
  data: function () {
    return {};
  },
  methods: {
    makeToast(append = false) {
      this.toastCount++;
      this.$bvToast.toast(`Feature Coming Soon`, {
        title: "Notification",
        autoHideDelay: 5000,
        appendToast: append,
      });
    },
  },
});

export default vm;
